// api.ts

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001/api';

// Module-scoped access token
let accessToken: string | null = null;

export function setAccessToken(token: string) {
  accessToken = token;
}

async function fetchWithRetry(
  endpoint: string,
  options: RequestInit,
  retry = true
): Promise<any> {
  const response = await fetch(`${API_BASE_URL}${endpoint}`, {
    ...options,
    credentials: 'include',
    headers: {
      ...(options.headers || {}),
      ...(accessToken ? { Authorization: `Bearer ${accessToken}` } : {}),
    },
  });

  if (response.status === 401 && retry) {
    // Try to refresh token
    const refreshRes = await fetch(`${API_BASE_URL}/auth/refresh`, {
      method: 'POST',
      credentials: 'include',
    });

    if (refreshRes.ok) {
      const { access_token } = await refreshRes.json();
      setAccessToken(access_token);

      // Retry original request with new token
      return fetchWithRetry(endpoint, options, false);
    } else {
      throw new Error('Unauthorized and refresh token failed');
    }
  }

  if (!response.ok) {
    throw new Error(`HTTP error! status: ${response.status}`);
  }

  return response.json();
}

export const apiClient = {
  get(endpoint: string) {
    return fetchWithRetry(endpoint, { method: 'GET' });
  },

  post(endpoint: string, data: any) {
    return fetchWithRetry(endpoint, {
      method: 'POST',
      body: JSON.stringify(data),
      headers: {
        'Content-Type': 'application/json',
      },
    });
  },

  put(endpoint: string, data: any) {
    return fetchWithRetry(endpoint, {
      method: 'PUT',
      body: JSON.stringify(data),
      headers: {
        'Content-Type': 'application/json',
      },
    });
  },

  delete(endpoint: string) {
    return fetchWithRetry(endpoint, { method: 'DELETE' });
  },
};

export default apiClient;
